.cont-aal{
    /*border:1px solid green;*/
    position: relative;
  }
  .fixed {
      position: fixed; 
      top: 182px; 
      height: 70px; 
      z-index: 1;
  }
  .faq--header{
    background: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1)),
      url("https://themeydanhotel.com/sites/default/files/2018-03/Gym.jpg");
    height: 50vh;
    max-height: 700px;
    margin-top:85px;
    background-position: center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-align: center;
    padding: 5rem calc((100vw-1300px)/2);
  }
  .faq--header h1{
    font-size: clamp(3rem, 8vw, 5rem);
    font-weight: bold;
    font-family:"Oswald",sans-serif;
  }
  .faq--navbar{
    position:relative;
    width: 100%;
    margin-top:0rem;
      background: rgb(250, 250, 250);
  }
  .faq--navbar ul{
    /*border:1px solid yellow;*/
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    max-width:1500px;
    margin:auto;
  }
  .faq--navbar ul{
    list-style: none;
   
  }
  .faq--navbar ul li a{
    display: block;
    color: black;
    font-family:"Oswald",sans-serif;
    text-decoration: none;
    border-bottom:5px solid transparent;
    padding:1rem 0.4rem ;
    font-size: clamp(1rem, 2.5vw, 2rem);
  }
  
   .faq--navbar ul li a:hover{
    border-bottom:5px solid #028851;
    color:#028851;
    transition: 0.3s ease-in-out;
    /*background-color: white;*/
  }
  .faq--navbar ul li a:active{
    border-bottom:5px solid #FFCD00;
    color:#FFCD00;
    /*background-color: white;*/
  }
  
  .main--data{
    /* using flex because of better browser support */
    display: flex;
    justify-content: center;
    align-items: center;
    /*border:1px solid red;*/
    margin:0rem 1rem;
    padding:2rem calc((100vw-1100px)/2);
  }
  
  .container--faq {
    margin: 5rem auto;
    background: #fff;
    border-radius: 0.25rem;
    padding:2rem 0rem;
    overflow-x: hidden;
    max-width: 1100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    /*border:1px solid black;*/
  }
  
  .container--faq  h3 {
    font-size: clamp(1.6rem, 4vw, 3rem);
    font-weight: bold;
    font-family:"Oswald",sans-serif;
    text-transform: capitalize;
  }
  
  .info{
    /*border:1px solid green;*/
    margin: auto;
    max-width:920px;
    text-align: left;
  }
  
  /*@media screen and (min-width: 1700px) {
    .container--faq{
      margin-left: -10px;
    }
  }*/
  
  
  @media screen and (max-width: 768px) {
    .container--faq {
      width:95%;
      margin:3rem auto 1rem auto;
    }
  }
  
  
  
  