.section {
    width: 90vw;
    margin: 4rem auto;
    max-width:  1050px;
    /*border:1px solid black;*/
    padding:3rem 0rem;
  }
  .fet--contain{
    background: rgba(250, 250, 250,0.4);
  }
  .title{
    /*border:1px solid red;*/
    display:flex;
    justify-content: center;
    flex-direction: column;
    max-width:  1050px;
    margin:auto;
    padding:3rem  1rem 3rem 1rem;
  }
  .title h2{
    font-family: "oswald",sans-serif;
    font-size: clamp(2rem, 4vw, 5rem); 
    text-transform: capitalize;
    text-align: center;
    padding:1rem 0rem;
  
  }
   .underline {
    width: 10rem;
    height: 0.25rem;
    margin-bottom: 1.5rem;
    background: rgba(2, 136, 81,1);
    margin:auto;
  }
  .ani{
    transform: translateX(-100%);
    margin-top: 1.5rem;
    opacity: 0;
  }
  span{
    color:rgba(2, 136, 81,1);
    font-weight: bold;
  }
  .title p{
    line-height: 1.5;
    padding-top:0.8rem;
    margin:0.5rem 0;
    font-family: "Montserrat",sans-serif;
    font-size: clamp(1.2rem, 2.5vw, 1.5rem); 
  }
  p.top{
    margin-top: 2.5rem;
  }
  
  .jobs-center{
    width: 80vw;
    margin: 0 auto;
    max-width: 1100px;
    /*border:1px solid yellow;*/
  }
  
  .btn-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 4rem;
    flex-wrap: wrap;
  }
  .job-btn {
    background: transparent;
    border-color: transparent;
    text-transform: capitalize;
    font-family: "Oswald",sans-serif;
    font-weight: bold;
    font-size: 1.25rem;
    letter-spacing:0.1rem;
    margin: 0 0.5rem;
    cursor: pointer;
    padding: 0.25rem 0.25rem;
    line-height: 1;
    outline-color:transparent;
    /*outline:none;*/
  }
  .job-btn:hover {
    color: #FFCD00;
    box-shadow: 0 2px #FFCD00;
    
  }
  .active-btn {
    color: #028851;
    box-shadow: 0 2px #028851;
  }
  .job-info{
    border:1px dashed rgba(0, 128, 0,0.2);
    max-width:1000px;
    margin:auto;
    height:100%;
    transition: all 0.3s ease-in;
  }
  .job-desc {
    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: 2rem;
    align-items: flex-start;
    margin-bottom: 1.25rem;
    transition: all 0.3s ease-in;
  }
  .job-info h3{
    margin-bottom:2rem;
    padding:1rem 0rem;
    display:inline-block;
    border-bottom: 5px solid #028851;
  }
  .job-icon{
    color:#028851;
    margin-top: 5px;
  }
  .job-desc p{
    font-family: "Montserrat",sans-serif;
    font-size: clamp(0.8rem, 2.5vw, 1.5rem); 
     margin-bottom: 15px;
     text-align: left;
     /*border:1px solid pink;*/
     color: black;
    
  }
  
  @media screen and (min-width: 992px) {
    .section {
      width: 95vw;
    }
    .jobs-center {
      width: 90vw;
      display: grid;
      grid-template-columns: 200px 1fr;
      column-gap: 1rem;
    }
    .btn-container {
      flex-direction: column;
      justify-content: flex-start;
    }
    .job-info{
     max-width:800px;
     margin-left:0px;
    }
      .job-btn {
      padding:1.5rem 0;
      margin-bottom: 1rem;
    }
    .active-btn {
      box-shadow: -2px 0 rgba(2, 136, 81,0);
       
    }
    .job-btn:hover {
      box-shadow: -2px 0 #FFCD00;
    }
  }
  
  @media screen and (max-width:630px)
  {.job-btn{
    margin-bottom: 1rem;
  }
    .btn-container{
      margin-bottom:2.5rem;
      margin-top: 0rem;
    }
  }