.team{
    padding :8rem calc((100vw-1100px)/2);
  }
  
  .team--wrapper{
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(auto,350px);
    /*border:1px solid red;*/
    max-width: 1300px;
    margin:2rem auto;
  }
  .team--card{
    margin: 1rem;
    border-radius: 10px;
    position:relative;
  
    opacity: 0;
    /*border:1px solid red;*/
  }
  .team--text{
    padding:1rem;
  }
  .team--text:nth-child(1){
    grid-row: 1/3;
    grid-column: 2/3;
  }
  .team--text:nth-child(2){
    grid-row: 5/6;
    grid-column: 1/2;
  }
  .team--card:nth-child(3){
    grid-row: 1/3;
    grid-column: 1/2;
    transform: translateX(-10%);
    opacity:0;
      
  }
  .team--card:nth-child(4){
    grid-row: 2/4;
    grid-column: 2/3;
    transform: translateX(3%);
    overflow: hidden;
    right: 0;
    opacity:0;
  }
  .team--card:nth-child(5){
    grid-row: 3/5;
    grid-column: 1/2;
     transform: translateX(-10%);
     opacity:0;
  }
  .team--card:nth-child(6){
    grid-row: 4/6;
    grid-column: 2/3;
    transform: translateX(3%);
    overflow: hidden;
    right: 0;
    opacity:0;
  }
  
  .team--text h2{
    color:#028851;
    font-family: "Oswald",sans-serif;
    font-weight: bold;
    margin-bottom: 0.5rem;
    font-size: clamp(1rem, 4vw, 2rem);
  }
  
  .team--text h1{
    margin-bottom: 1.5rem;
    font-size: clamp(2rem, 6vw, 4rem);
    font-family: "Oswald",sans-serif;
  }
  .team--text p{
    margin-bottom: 1.2rem;
    margin-left: 0.8rem;
    line-height: 1.4;
    font-family: "Montserrat",sans-serif;
    font-size: clamp(1rem, 3vw, 1.3rem); 
  }
  
  .team--card p{
     font-family: "Oswald",sans-serif;
     font-weight: bold;
     color:#fff;
     position: absolute;
     bottom:15px;
     overflow: hidden;
     left:0px;
     width:100%;
     font-size: clamp(1.5rem, 4vw, 3rem); 
     background-color: rgba(0,0,0,0.4);
     display: inline-block;
     padding: 1rem 0.2rem;
  }
  
  .team--image{
    width:100%;
    height: 100%;
    border-radius: 10px;
  
  }
  
  @media screen and (max-width:768px){
    .team--wrapper{
      grid-template-columns:1fr;
      grid-template-rows: auto;
    }
    .team--text{
    padding:1rem;
  }
  .team--text p{
    margin-left: 0rem;
  }
  .team--text:nth-child(1){
    grid-row: 1/2;
    grid-column: 1/2;
  }
  .team--text:nth-child(2){
    grid-row:6/7;
    grid-column: 1/2;
  }
  .team--card:nth-child(3){
    grid-row: 3/4;
    grid-column: 1/2;
  }
  .team--card:nth-child(4){
    grid-row: 4/5;
    grid-column: 1/2;
  }
  .team--card:nth-child(5){
    grid-row: 5/6;
    grid-column: 1/2;
  }
  .team--card:nth-child(6){
    grid-row: 2/3;
    grid-column: 1/2;
  }
    }