.reviews--container{
    /*border:1px solid red;*/
    padding:1.5rem calc((100vw - 1300px) / 2);
    overflow-x:hidden ;
    margin:0 auto;
  }
  .review-title {
    text-align: center;
    margin: 2rem auto;
  }
  
  .review-title h2{
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-family: "oswald",sans-serif;
    color:#028851;
    font-size: clamp(2rem, 3.5vw, 3rem);
  }
  section {
    width: 90vw;
    margin: 5rem auto;
    max-width: 1170px;
    overflow: hidden;
    text-align: center;
  }
  .section-center {
    margin: 0 auto;
    margin-top: 4rem;
    width: 80vw;
    height: 450px;
    max-width: 800px;
    text-align: center;
    justify-content: center;
    align-items: center;
    position: relative;
    display: flex;
    overflow: hidden;
    /*border:1px solid green;*/
  }
  
  .person-img {
    border-radius: 50%;
    margin-bottom: 1rem;
    width: 150px;
    height: 150px;
    object-fit: cover;
    border: 4px solid  rgba(2, 136, 81,.75);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  }
  article h4 {
    text-transform: uppercase;
    color: #028851;
    margin-bottom: 0.25rem;
    font-family: "oswald",sans-serif;
    font-size: clamp(1.5rem, 2vw, 3rem);
  }
  
  .text {
    max-width: 35em;
    margin: 0 auto;
    margin-top: 2rem;
    line-height: 2;
    color:#000;
    font-family:"Montserrat",sans-serif;
  }
  p.title_p{
   text-transform: capitalize;
    margin-bottom: 0.75rem;
    margin-top:0.5rem;
    color: #000;
    font-family:"Montserrat",sans-serif;
    font-weight: 700;
    font-size: clamp(1rem, 0.8vw, 1.4rem);
  }
  .icon {
    font-size: 3.2rem;
    margin-top: 1rem;
    color: #028851;
  }
  
  .prev,
  .next {
    position: absolute;
    top: 200px;
    transform: translateY(-50%);
    width: 1.25rem;
    height: 1.25rem;
    display: grid;
    /*border:1px solid black;*/
    place-items: center;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s linear;
  }
  .prev {
    left: 0;
    transform: translateX(0%);
  }
  .next {
    right: 0;
    transform: translateX(-140%);
  }
  article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    margin:auto;
    transition: all 0.3s linear;
  }
  article.activeSlide {
    opacity: 1;
    transform: translateX(0);
  }
  article.lastSlide {
    transform: translateX(-100%);
    
  }
  article.nextSlide {
    transform: translateX(100%);
    
  }
  
  
  /**/
  
  @media screen and (min-width: 1700px) {
    .section-center{
      margin-left: -25px;
    }
  }
  @media screen and (min-width: 1800px) {
    .section-center{
      margin-left: -85px;
    }
  }
  @media screen and (min-width: 2000px) {
    .section-center{
      margin-left: -200px;
    }
  }
  @media screen and (min-width: 2500px) {
    .section-center{
      margin-left: -400px;
    }
  }
  @media screen and (min-width: 992px) {
    section {
      width: 95vw;
    }
  }
  @media (min-width: 800px) {
    .text {
      max-width: 45em;
    }
    .prev,
    .next {
      width: 2rem;
      height: 2rem;
      font-size: 1.5rem;
    }
  }
  @media (max-width: 550px){
   .section-center{
    height: 500px;
   }
  }