.membership {
    /*border:1px solid white;*/
    background: #001001;
    color: #fff;
    padding: 6rem calc((100vw-1100px) / 2);
    text-align: center;
  }
  
  .membership--container {
    /*border:1px solid yellow;*/
    margin: 3rem auto;
    padding: 2rem 0;
  }
  
  .membership--container h1 {
    margin-bottom: 1rem;
    font-family: "Oswald", sans-serif;
    font-weight: bold;
    font-size: clamp(2rem, 5vw, 4rem);
    padding: 0 1rem;
  }
  .membership--desc {
    font-family: "Montserrat", sans-serif;
    margin-bottom: 3rem;
    padding: 1rem 0;
    font-size: clamp(1rem, 2.5vw, 1.5rem);
    width: 80%;
    margin: auto;
  }
  
  .membership--wrap {
    display: flex;
    flex-wrap: nowrap;
    /*border:1px solid red;*/
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
    opacity: 0;
    transform: translateY(-15%);
  }
  .membership--title {
    /*border:1px solid pink;*/
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    width: 80%;
    height: 150px;
    margin: 0 auto;
  }
  .membership--card {
    /*border:1px solid blue;*/
    padding: 2rem;
    border-radius: 10px;
    /*min-height:450px;*/
    min-height: 560px;
    width: 325px;
    margin: 1rem 1rem;
    color: #000;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-center;
  }
  /*.membership--card > div{
      border:1px solid black;
    
    }*/
  
  .membership--title {
    margin: 0 auto 0 auto;
  }
  
  h3 {
    font-family: "Oswald", sans-serif;
    font-weight: bold;
    font-size: clamp(1.6rem, 2.5vw, 3rem);
  }
  
  .membership--perk > h3 {
    margin-bottom: 1rem;
    font-family: "Oswald", sans-serif;
    font-weight: bold;
    font-size: clamp(1.4rem, 2.5vw, 3rem);
    display: inline-block;
  
    /*display:flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;*/
    text-align: center;
  
    /*border:1px solid red;*/
  }
  .membership--perk {
    width: 80%;
    height: 50px;
    margin: 0 auto;
  }
  
  .membership--perk > h3 > span {
    color: #ffcd00;
    font-weight: bold;
    font-size: clamp(2rem, 3.5vw, 5rem);
  }
  .membership--perk--desc {
    /*border:1px solid green;*/
    margin: 2rem auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    width: 95%;
    height: 250px;
    margin-top: 0rem;
    font-family: "Montserrat", sans-serif;
  }
  .membership--perk > h3 > span > small {
    font-size: clamp(0.9rem, 1.5vw, 1.2rem);
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  li {
    font-family: "Montserrat", sans-serif;
    margin: 0.5rem auto;
  }
  
  @media screen and (min-width: 1500px) {
    .membership--perk--desc {
      margin-top: 3rem;
    }
  }
  
  @media screen and (max-width: 950px) {
    .membership--wrap {
      flex-direction: column;
    }
    .membership--card {
      margin: 2rem auto;
      min-height: 380px;
    }
    .membership--card > div {
      padding: 1rem 0;
    }
    .membership--perk--desc {
      margin-top: 1rem;
    }
  }
  @media screen and (max-width: 450px) {
    .membership--card {
      width: 80%;
    }
  }
  